import React, { useState, useEffect } from "react";
import axios from "axios";
import { styled } from "@mui/material/styles";
import Divider from "@mui/material/Divider";
import List from "@mui/material/List";
import ContactPageIcon from "@mui/icons-material/ContactPage";
import LinkButton from "../Componets/LinkButton";
import ListColapse from "../Componets/ListColapse";
import { Typography } from "@mui/material";
import ReadMD from "../Componets/ReadMD";


const FireNav = styled(List)({
	"& .MuiListItemButton-root": {
		paddingLeft: 4,
		paddingRight: 4,
	},
	"& .MuiListItemIcon-root": {
		minWidth: 0,
		marginRight: 12,
	},
	"& .MuiSvgIcon-root": {
		fontSize: 25,
	},
});

export default function Home(props) {
	const { lang, env } = props;
	const [listJobs, setListJobs] = useState([]);
	const [listEducation, setListEducation] = useState([]);

	useEffect(() => {
		axios
			.get(
				`https://raw.githubusercontent.com/afimpel/afimpel.github.io/cv/CurriculumVitae/Jobs/lists.json`
			)
			.then(function (response) {
				// handle success
				let res = response.data;
				setListJobs(res);
			})
			.catch(function (error) {
				// handle error
				console.error(error);
				setListJobs([]);
			});
		axios
			.get(
				`https://raw.githubusercontent.com/afimpel/afimpel.github.io/cv/CurriculumVitae/Education/lists.json`
			)
			.then(function (response) {
				// handle success
				let res = response.data;
				setListEducation(res);
			})
			.catch(function (error) {
				// handle error
				console.error(error);
				setListEducation([]);
			});
	}, []);

	return (
		<>
			<Typography
				variant="h1"
				fontWeight={700}
				component="h1"
				sx={{ my: 0, mx: 0, textAlign: "center" }}
			>
				{lang.fullname}
			</Typography>
			<ReadMD
				sx={{ p: 0 }}
				replaceList={[
					{ origen: "changeMe", replace: new Date().getFullYear() - 2004 },
				]}
				env={env}
				file={`https://raw.githubusercontent.com/afimpel/afimpel.github.io/cv/CurriculumVitae/resume_${lang.type}.md`}
			/>
			<FireNav component="nav" disablePadding>
				<LinkButton
					key={1000}
					to={"/cv"}
					alternative={"/cv"}
					iconSRC={<ContactPageIcon />}
					icons={true}
					type="list"
					primary={lang.cv.title}
					dense={true}
					primaryTypographyProps={{
						fontSize: 24,
						fontWeight: "medium",
					}}
				/>
				<Divider />
				<ListColapse
					list={listJobs}
					primary={lang.cv.Jobs}
					close={true}
					disabled={true}
				/>
				<Divider />
				<ListColapse
					list={listEducation}
					primary={lang.cv.Education}
					close={true}
					disabled={true}
				/>
				<Divider />
			</FireNav>
		</>
	);
}
